.promo {
  margin: 35px auto;
  position: relative;
}

.promo__text {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: $white;
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  width: 162px;
}

.promo__icon {
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: $tablet-size) {
  .promo {
    margin: 69px 0 76px;
  }

  .promo__icon {
    left: 79px;
    top: 57px;
    transform: none;
  }

  .promo__text {
    text-align: left;
    left: 423px;
    top: 70px;
    transform: none;
  }
}

@media (min-width: $desktop-size) {
  .promo {
    margin: 0;
    align-self: start;
  }

  .promo__icon, .promo__text {
    left: 50%;
    transform: translateX(-50%);
  }

  .promo__text {
    text-align: center;
    top: 187px;
  }
}
